<template>
  <div class="slide-content">
    <div class="slide-view">
      <div class="btn btn-left" @click="prePic" >&lt;</div>
      <img v-for="(item, index) in slidelist"  :key="index" alt="" v-show="index==num" :src="item">
      <div class="btn btn-right" @click="nextPic">></div>
    </div>
  </div>
</template>
<style scoped>
img{
    width:85%;
    height: 30%;
}
.slide-content{
    margin: 0;
    padding: 0;
    width: 100%;
}
.slide-view img{
    margin: 5px auto;
    display: flex;
    justify-content: center;
}
.slide-view .btn{
    display: flex;
    width: 10%;
    height: 60px;
    color: white;
    background: rgba(0,0,0,0.5);
    position: absolute;
    top: calc(50% - 30px);
    line-height: 60px;
    text-align: center;
    font-size: 20px;
    font-family: "宋体";
    cursor: pointer;
}
.slide-view .btn-left{
  display: flex;
  left: 8%;
  align-items: center;
}
.slide-view .btn-right{
  display: flex;
  right: 8%;
  align-items: center;
}
</style>
<script>
export default {
  name: 'Slide',
  data() {
    return {
      slidelist: [
        require('../../assets/images/1.jpg'),
        require('../../assets/images/2.jpg'),
        require('../../assets/images/3.jpg'),
        require('../../assets/images/4.jpg')
      ],
      num: 0
    }
  },
  methods: {
    go() {
      setInterval(() => {
        this.num++
        if (this.num === this.slidelist.length) {
          this.num = 0
        }
      }, 2000)
    },
    prePic() {
      if (this.num === 0) {
        this.num = this.slidelist.length - 1
      } else {
        this.num--
      }
    },
    nextPic() {
      if (this.num === this.slidelist.length - 1) {
        this.num = 0
      } else {
        this.num++
      }
    }
  },
  created() {
    this.go()
  }
}
</script>



