<template>
  <div class="home-content">
        <div class="home-top-ground">
          <div class="home-nav">
            设置
            <input type="text" class="home-search">
            登录
        </div>
      </div>
      <div class="grid-content">
        <div class="grid-ground">
            <div class="grid-chart">
                <ul>
                    <li v-for=" (item,index) in gridlist " :key="index">
                        <div class="grid-box" :style="{background:item.gridColor, color:item.color}" >
