  align-items: center;
  justify-content: space-around;
}
.home-content{
  margin: 0px;
  padding: 0px;
}
.home-top-ground{
background:linear-gradient(aqua,rgb(14, 14, 206,0.3));
width: 100%;
height: 50px;
z-index: -1;
border-bottom-left-radius: 20px;
border-bottom-right-radius: 20px;
}
.home-nav{
