  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.home-search{
  width:60%;
  margin: 0px 10px;
  border-radius: 20px;
}
.grid-content{
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.grid-ground{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 90%;
  height: 40%;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background:linear-gradient(rgb(14, 14, 206,0.4),rgba(52, 232, 245, 0.3));
}
.grid-chart{
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.grid-chart ul{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}
.grid-chart ul li{
  margin: 5px;
  width: 17%;
  max-width: 80px;
  max-height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.grid-box{
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
}
.recommend-content{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  justify-items: center;
  padding-bottom: 40px;
  margin-top: 10px;

}
.recommend-content li{
  width: 46%;
  flex-direction: column;
  float: left;
  height: auto;
}
.recommend-top img{
  width: 98%;
  height: 98%;
  background-size: contain;
}
.recommend-top p{
  font-size: 18px;
  width: 98%;
  height: 98%;
}
.recommend-details{
  width: 98%;
  height: 20px;
  font-size: small;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
}
.recommend-bottom{
  display: flex;
  height: 35px;
  justify-content: space-between;
  padding: 0 4px;
  align-items: center;
}
.recommend-price{
  font-weight: bold;
  color: red;
}
.recommend-see{
  border: 0.5px solid black;
  height: 18px;
  padding: 2px;
  text-align: center;
  font-size: 12px;
}
</style>
