        {id: '1', url: require('../../assets/home-recommed/1.jpg'), price: 4399, recommendText: '这个商品设00000000000000000000000000000000000000'},
        {id: '2', url: require('../../assets/home-recommed/4.jpg'), price: 1233, recommendText: '这个商品设000000000000000000000000000000000'},
        {id: '3', url: require('../../assets/home-recommed/2.jpg'), price: 2166, recommendText: '这个商品设00000000'},
        {id: '4', url: require('../../assets/home-recommed/3.jpg'), price: 5644, recommendText: '这个商品设00000000'},
        {id: '5', url: require('../../assets/home-recommed/5.jpg'), price: 4164, recommendText: '这个商品设00000000'},
        {id: '6', url: require('../../assets/home-recommed/6.jpg'), price: 4399, recommendText: '这个商品设00000000'}
      ]
    }
  }
}
</script>



